@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
*{padding:0;margin:0;outline:0;box-sizing:border-box;font-family:'Poppins', sans-serif}body{width:100vw;height:100vh;background-color:#fccde2;overflow:hidden;display:grid;place-items:center}.main-container{background-color:#fcefee;width:1400px;height:800px;border-radius:20px;box-shadow:0px 0px 20px 0px rgba(0,0,0,0.2)}@media screen and (max-width: 1400px){.main-container{width:100vw;height:100vh;min-height:auto;border-radius:0%}}.app-header{width:100%;padding:10px 20px;display:flex;justify-content:space-between;align-items:center}.app-header h1{color:#364f6b;display:inline-block;cursor:default}.app-header h5{color:#fc5c9c;display:inline-block;cursor:default}.drawer-wrapper{width:100%;padding:20px 50px;height:70%}@media screen and (max-width: 600px){.drawer-wrapper{padding:20px}}.colors-container{width:100%;display:flex;align-items:center}.color{width:30px;height:30px;border:2px solid #364f6b;border-radius:50%;margin-right:5px;cursor:pointer;transition:200ms}.color:hover{border-color:#fcefee}.color-selected{width:30px;height:30px;border:2px solid #364f6b;border-radius:50%;margin-right:5px;color:#364f6b;position:relative;overflow:hidden}.color-selected::after{width:100%;height:100%;display:grid;place-items:center;position:absolute;top:0;left:0;background-color:rgba(0,0,0,0.2);content:"\2714";font-size:18px}.eraser{width:30px;height:30px;border:2px solid #364f6b;border-radius:50%;margin-right:5px;color:#364f6b;position:relative;overflow:hidden;cursor:pointer;transition:200ms}.eraser:hover{border-color:#fcefee}.eraser::after{width:100%;height:100%;display:grid;place-items:center;position:absolute;top:0;left:0;content:"\271C";font-size:30px;line-height:26px}.eraser-selected{width:30px;height:30px;border:2px solid #364f6b;border-radius:50%;margin-right:5px;color:#364f6b;position:relative;overflow:hidden}.eraser-selected::after{width:100%;height:100%;display:grid;place-items:center;position:absolute;top:0;left:0;background-color:rgba(0,0,0,0.2);content:"\271C";font-size:30px;line-height:26px}.clear{width:30px;height:30px;border:2px solid #364f6b;border-radius:50%;margin-right:5px;color:#364f6b;position:relative;overflow:hidden;cursor:pointer;transition:200ms}.clear:hover{border-color:#fcefee}.clear::after{width:100%;height:100%;display:grid;place-items:center;position:absolute;top:0;left:0;content:"\2716";font-size:30px;line-height:26px}#color-red{background-color:#f44336}#color-yellow{background-color:#ffeb3b}#color-blue{background-color:#304ffe}#color-green{background-color:#00897b}#color-orange{background-color:#ff5722}.canvas-container{border:3px solid #364f6b;margin-top:10px}#drawing-canvas{background-color:#e6e6d6;width:100%;height:100%}.app-footer{width:100%;padding:10px;display:grid;place-items:center}.app-footer button{border:0;width:150px;height:40px;background-color:#fc5c9c;font-size:16px;color:#fcefee;display:grid;place-items:center;border-radius:5px;transition:200ms;cursor:pointer}.app-footer button:hover{box-shadow:0px 0px 10px 0px rgba(0,0,0,0.2)}

